.order {
    &_form {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 30px;
        grid-row-gap: 18px;

        &_item {
            label {
                display: block;
                font-weight: 600;
                color: var(--dark);
                margin-bottom: 8px;
            }

            input,
            select,
            button {
                -webkit-appearance: button;
                width: 100%;
                font-size: 15px;
                line-height: 25px;
                padding: 12px 15px 12px 12px;
                color: var(--lightBlack);
                border-radius: 6px;
                border: 1px solid transparent;
                background: var(--gray);
                height: 50px;
            }

            select {
                background: #F2F2F2 url(../../../assets/img/down.png) no-repeat 95% 52%;
                -webkit-appearance: none;
            }

            input[type=date] {
                background: #F2F2F2 url(../../../assets/img/date.png) 95% 50% no-repeat;
                -webkit-appearance: none;
            }

            button {
                color: var(--white);
                background: var(--blue);

                &:hover {
                    background: var(--red);
                }
            }
        }
    }
}


@media (max-width: 767px) {
    .order {
        &_form {
            grid-template-columns: 1fr;
        }
    }
}

@media (max-width: 575px) {
    .order {
        &_form {
            grid-row-gap: 33px;
        }
    }
}

@media (max-width: 384px) {
    .order {
        &_form_item button {
            margin: 10px 0;
        }
    }
}